export default {
	// 
	namespaced: true,

	//  state 
	state: () => ({
		// 
		//  6 
		// { goods_id, goods_name, goods_price, goods_count, goods_small_logo, goods_state }
		// cart: JSON.parse(uni.getStorageSync('cart') || '[]'),
		cart: [],
	}),

	//  mutations 
	// statemutations
	mutations: {
		addToCart(state, goods) {
			// Id
			//  findResult  undefined
			const findResult = state.cart.find((x) => x.goods_id === goods.goods_id)


			if (!findResult) {
				//  push
				state.cart.push(goods)
			} else {
				// 
				findResult.goods_count++
			}
			// console.log(state.cart)
			//  commit  m_cart  saveToStorage 
			this.commit('m_cart/saveToStorage')
			// todo 
		},
		// 
		saveToStorage(state) {
			uni.setStorageSync('cart', JSON.stringify(state.cart))
		},
		// 
		updateGoodsState(state, goods) {
			//  goods_id 
			const findResult = state.cart.find(x => x.goods_id === goods.goods_id)

			// 
			if (findResult) {
				// 
				findResult.goods_state = goods.goods_state
				// 
				this.commit('m_cart/saveToStorage')
				console.log("")
			}
		},
		// 
		updateGoodsCount(state, goods) {
			//  goods_id 
			const findResult = state.cart.find(x => x.goods_id === goods.goods_id)

			// 
			if (findResult) {
				// 
				findResult.goods_count = goods.goods_count
				// 
				this.commit('m_cart/saveToStorage')
				console.log("")
			}
		},
		//  Id 
		removeGoodsById(state, goods_id) {
			//  filter 
			state.cart = state.cart.filter(x => x.goods_id !== goods_id)
			// 
			this.commit('m_cart/saveToStorage')
		},
		// 
		async updateAllGoodsState(state, params) {
			// // 
			// state.cart.forEach(x => x.goods_state = newState)
			// // 
			// this.commit('m_cart/saveToStorage')
			// console.log(params)
			let user_id = params.user_id
			let newState = params.newState
			const {
				data: res
			} = await uni.$http.get('/api/cart/updateAllGoodsState', {
				user_id,
				newState
			})
			if (res.meta.status !== 200) {
				return uni.$showMsg("")
			}
			// 
			this.commit('m_cart/getCartList', {
				user_id: user_id
			})
		},
		// 
		updateCartInfo(state, cartList) {
			state.cart = cartList
		},
		// 
		async getCartList(state, params) {
			let user_id = params.user_id
			const {
				data: res
			} = await uni.$http.get('/api/cart/getCartList', {
				user_id
			})

			if (res.meta.status !== 200) {
				return uni.$showMsg('')
			}
			this.commit('m_cart/updateCartInfo', res.message)
		}
	},

	//  getters 
	getters: {
		// 
		total(state) {
			let c = 0
			//  c 
			state.cart.forEach(goods => c += goods.goods_count)
			return c
		},
		// 
		checkedCount(state) {
			//  filter 
			//  reduce 
			// reduce() 
			return state.cart.filter(x => x.goods_state).reduce((total, item) => total += item.goods_count, 0)
		},
		// 
		checkedGoodsAmount(state) {
			//  filter 
			//  reduce  * 
			// reduce() 
			//  toFixed(2) 
			return state.cart.filter(x => x.goods_state)
				.reduce((total, item) => total += item.goods_count * item.goods_price, 0)
				.toFixed(2)
		}
	},
}