//  mapGetters 
import {
	mapGetters
} from 'vuex'

export default {
	computed: {
		//  m_cart  total 
		...mapGetters('m_cart', ['total']),
	},
	watch: {
		//  total 
		total() {
			//  methods  setBadge  tabBar 
			this.setBadge()
		},
	},
	onShow() {
		// 
		this.setBadge()
	},
	methods: {
		setBadge() {
			//  uni.setTabBarBadge() 
			uni.setTabBarBadge({
				index: 2, // 
				text: this.total + '' // text 
			})
		}
	},
}