export default {
	// 
	namespaced: true,

	// state 
	state: () => ({
		// 3.  address 
		// address: JSON.parse(uni.getStorageSync('address') || '{}'),
		// address: {
		// 	"userName": "",
		// 	"telNumber": "13652418912",
		// 	"provinceName": "",
		// 	"cityName": "",
		// 	"countyName": "",
		// 	"detailInfo": ""
		// },
		address: {},
		//  token  openid  session_key
		token: '',
		openid: '',
		session_key: '',
		user_id: null, // id
		// 
		userinfo: {},
		// userinfo: {
		// 	"nickName": "",
		// 	"phone": "",
		// 	"gender": 0,
		// 	"language": "",
		// 	"city": "",
		// 	"province": "",
		// 	"country": "",
		// 	"avatarUrl": "https://thirdwx.qlogo.cn/mmopen/vi_32/POgEwh4mIHO4nibH0KlMECNjjGxQUq24ZEaGT4poC6icRiccVGKSyXwibcPq4BWmiaIGuG1icwxaQX6grC9VemZoJ8rg/132"
		// },
		//  object  { openType, from }
		redirectInfo: null
	}),

	// 
	mutations: {
		saveUserToken(state, token) {
			state.token = token
		},
		saveOpenId(state, openid) {
			state.openid = openid
		},
		saveSessionKey(state, session_key) {
			state.session_key = session_key
		},
		// 
		async getAddress(state) {
			let user_id = state.user_id
			const {
				data: res
			} = await uni.$http.get('/api/address/getAddress', {
				user_id
			})
			if (res.meta.status !== 200) {
				uni.$showMsg('')
			}
			if (res.message != null) {
				state.address = res.message
			}
			// console.log('')
			// console.log(state.address)

		},
		// 
		updateAddress(state, address) {
			state.address = address
			// 2.  this.commit()  m_user  saveAddressToStorage  address 
			this.commit('m_user/saveAddressToStorage')
		},
		// 
		async updateAddressToDB(state, address) {
			let user_id = state.user_id
			const {
				data: res
			} = await uni.$http.post('/api/address/updataDefaultAdd', address)
			if (res.meta.status !== 200) {
				return uni.$showMsg('')
			}
			// 
			// this.getAddress()
			this.commit('m_user/getAddress')

		},
		// 1.  address  mutations 
		saveAddressToStorage(state) {
			uni.setStorageSync('address', JSON.stringify(state.address))
		},
		// 
		updateUserInfo(state, userinfo) {
			state.userinfo = userinfo
			//  this.commit()  m_user  saveUserInfoToStorage  userinfo 
			this.commit('m_user/saveUserInfoToStorage')
		},
		// id
		updateUserId(state, user_id) {
			state.user_id = user_id
		},
		//  userinfo 
		saveUserInfoToStorage(state) {
			uni.setStorageSync('userinfo', JSON.stringify(state.userinfo))
		},
		// 
		updateRedirectInfo(state, info) {
			state.redirectInfo = info
			// console.log(info)
			// console.log(this.redirectInfo)
		},
		// 
		setUserAvatar(state, avatarUrl) {
			state.userinfo.avatarUrl = avatarUrl
			// console.log("avatar = " + avatarUrl)
		},
		// 
		setUserName(state, nickName) {
			state.userinfo.nickName = nickName
			// console.log("nickName = " + nickName)
		}
	},

	// 
	getters: {
		// 
		addstr(state) {
			if (!state.address.provinceName) return ''

			//   
			return state.address.provinceName + state.address.cityName + state.address.countyName + state.address
				.detailInfo
		},
	},
}